.form {
  width: 100%;
  max-width: 100%;

  &__box {
    @include flex-sb;

    position: relative;
    margin-bottom: 50px;
  }

  &__input {
    position: relative;
    outline: 0;
    border: 1px solid var(--color-gray-400);
    border-radius: var(--br);
    padding: 10px 0 10px 16px;
    width: 100%;
    background-color: var(--color-white);

    &:focus {
      border: 1px solid var(--color-green);
    }
  }

  &__label {
    position: absolute;
    top: -30px;
  }
}

.form-search {
  display: inline-block;
  position: relative;
  width: 100%;
  max-width: 380px;
  border: 1px solid var(--color-gray-400);
  border-radius: var(--br);
  flex-shrink: 0;
  margin-right: clamp(20px, 3rem, 50px);
  margin-left: auto;

  @include for-width(-desktop-med) {
    max-width: 300px;
    margin-left: 0;
  }

  @include for-width(-desktop-s) {
    max-width: 200px;
    margin-right: 20px;
  }

  @include for-width(-mobile-md) {
    max-width: 120px;
  }

  @include for-width(-mobile-sl) {
    display: none;
    visibility: hidden;
    opacity: 0;
  }

  &__input {
    padding: 12px 0 12px 16px;
    width: 100%;

    @include for-width(-mobile-md) {
      padding: 8px;
    }

    &::placeholder {
      font-size: 14px;
      color: var(--color-gray-600);
    }

    &:focus {
      border: 1px solid var(--color-green);
      outline-color: transparent;
    }
  }

  &__btn {
    position: absolute;
    right: 12px;
    top: 50%;
    width: 16px;
    height: 16px;
    background-color: transparent;
    transform: translateY(-50%);
  }

  &__icon {
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    fill: var(--color-gray-800);
    transition: fill 0.3s linear;

    &:hover {
      fill: var(--color-green);
    }
  }
}

.form-modal {
  display: flex;
  flex-direction: column;
  gap: 50px;

  .btn {
    margin-bottom: 24px;
    max-width: 100%;
    width: 100%;
  }

  &__box {
    position: relative;

    .form__label {
      left: 0;
    }

    &--password {
      @include flex-all-sb;
    }
  }
}

.custom-checkbox {
  @include custom-checkbox;
}

.show-password {
  @include flex-all-center;
  position: absolute;
  right: 0;
  top: 25%;

  &__label {
    position: relative;
    cursor: pointer;
  }

  &__input {
    position: relative;
    outline: none;
    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      right: 14px;
      mask-image: url("../img/sprite.svg#eye");
      width: 24px;
      height: 24px;
      mask-position: center;
      mask-size: cover;
      mask-repeat: no-repeat;
      background-color: var(--color-gray-800);
      transition: mask-image 0.3s linear, background-color 0.3s linear;
    }

    &:checked::before {
      content: "";
      mask-image: url("../img/sprite.svg#closed-eye");
      background-color: var(--color-green);
    }

    &:hover::before {
      background-color: var(--color-green);
    }
  }
}

.product-form {
  margin-bottom: rem(24);

  &__list {
    @include flex-col;
    gap: 2rem;
  }

  &__item {
    &--select {
      @include flex-sb;
      align-items: end;
    }

    &--number {
      @include flex-all-sb;
      gap: 2rem;
    }
  }
}

.color-picker {
  @include flex;
  gap: 1rem;

  &__title {
    margin-bottom: 12px;
    color: var(--color-gray-800);
  }

  &__item {
    &--value {
      font-size: 14px;
      text-transform: capitalize;
      color: var(--color-gray-600);
      transition: all 0.3s linear;
    }
  }

  &__input {
    display: grid;
    place-content: center;
    border-radius: 100vw;
    width: 16px;
    height: 16px;
    background-color: var(--radio-color, currentcolor);
    transition: background-color 0.3s linear;
    cursor: pointer;

    &::before {
      content: "";
      border: 1px solid var(--radio-color, currentcolor);
      border-radius: 100vw;
      width: 24px;
      height: 24px;
      transform: scale(0);
      transition: 120ms transform ease-in-out;

    }

    &:checked::before {
      transform: scale(1);
    }

    &#pink {
      --radio-color: var(--color-pink);
    }

    &#blue {
      --radio-color: var(--color-blue-100);
    }

    &#yellow {
      --radio-color: var(--color-yellow);
    }
  }
}

.form-article {
  &__inner {
    @include flex-all-sb;

    margin-bottom: 20px;
  }
  .checkboxes {
    margin: 0;
  }

  .checkboxes__label {
    font-size: 14px;
    line-height: 1.5;
    padding: 8px;
    min-width: 24px;
    height: 24px;

  }
}
